body.body {
  overflow: hidden;
}

.container {
  max-width: 1116px;
  margin: 0 auto;
  padding: 0 10px;
}

.header {
  font-size: 14px;
  font-family: 'RobotoMedium';
  color: #557571;
}

.header_container {
  padding: 0 55px;
  margin: 0 auto;
}

.header_row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 0 16px;
}

.header_right {
  display: flex;
  align-items: center;
}

.header_geo {
  display: flex;
  align-items: center;
}

.header_geo p {
  max-width: 155px;
  margin-left: 5px;
  margin-bottom: 0;
}

.header_mail {
  display: flex;
  align-items: center;
  padding-left: 35px;
  border-left: 1px solid #E9E9E9;
  margin-left: 35px;
}

.header_mail a {
  text-decoration-line: underline;
  color: #557571;
  margin-left: 5px;
  margin-bottom: 0;
}

.header_mail a:hover {
  text-decoration: none;
}

.header_tel {
  display: flex;
  align-items: center;
  border-left: 1px solid #E9E9E9;
  padding-left: 30px;
  margin-left: 40px;
}

.header_tel .tel {
  font-size: 22px;
  line-height: 26px;
  letter-spacing: 0.065em;
  color: #F08771;
  margin-left: 5px;
  margin-bottom: 0;
}

.header_nav {
  background: #557571;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
}

.header_ul {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.header_li {
  margin: 0 12.5px;
}

.header_li a {
  font-size: 15px;
  line-height: 20px;
  text-transform: uppercase;
  color: #FFFFFF;
  font-family: 'OpenSansBold';
}

.header_li a:hover {
  text-decoration: none;
}

.header_li a::after {
  display: block;
  content: '';
  border-bottom: 1px solid #fff;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform 450ms ease-in-out;
}

.header_li a:hover::after {
  transform: scaleX(1);
  transform-origin: 0% 50%;
}

.btn_mnu {
  display: none;
}

.inner-intro {
  height: 100px;
  background: url(../img/about-intro.jpg) center / cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
}

.inner-intro_h1 {
  font-size: 24px;
  line-height: 143.88%;
  text-align: center;
  letter-spacing: 0.415em;
  text-transform: uppercase;
  color: #000000;
  padding: 0 10px;
  font-family: 'Roboto Mono';
  font-weight: bold;
}

.about {
  padding: 120px 110px;
}

.about_block {
  width: 100%;
  min-height: 740px;
  background: #fff;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
  padding: 106px 94px 138px 91px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin: auto;
}

.about_text {
  font-size: 18px;
  line-height: 24px;
  color: #557571;
  max-width: 535px;
  font-family: 'Roboto Mono';
  font-weight: normal;
}

.about_text h3 {
  font-family: 'Roboto Mono';
  font-weight: bold;
}

.about_text h3,
.about_text p {
  margin-bottom: 25px;
}

.about_img {
  position: relative;
}

.about_img--text {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  width: 350px;
  height: 87px;
  background: #557571;
  display: flex;
  align-items: center;
  padding-left: 25px;
}

.about_img--text p {
  max-width: 275px;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
  font-family: 'OpenSansSemiBold';
}

.btn {
  border: 3px solid #F08771;
  border-radius: 32px;
  background-color: #fff;
  text-align: center;
  padding: 13px 34px;
  font-size: 15px;
  line-height: 1;
  color: #F08771;
  text-transform: uppercase;
  font-family: 'OpenSansBold';
  display: inline-block;
  transition: .4s all;
}

.btn:hover {
  background-color: #F08771;
  color: #fff;
  text-decoration: none;
}

.bottom {
  background-color: #557571;
  position: relative;
  padding: 120px 0 130px;
}

.bottom::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../img/bottom.jpg) center / cover no-repeat;
  z-index: -1;
}

.bottom_block {
  width: 100%;
  background-color: #fff;
  box-shadow: 0px 0px 24px 19px rgba(0, 0, 0, 0.25);
  display: flex;
  margin: auto;
}

.bottom_text {
  margin: 70px 0 0 116px;
  max-width: 450px;
  padding-right: 15px;
}

.bottom_text p {
  font-size: 18px;
  line-height: 162.88%;
  color: #557571;
  font-family: 'Roboto Mono';
  font-weight: bold;
  margin-bottom: 30px;
}

.footer {
  min-height: 171px;
  background-color: #557571;
  padding: 28px 0;
}

.footer_row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer_p {
  margin-top: 7px;
  font-size: 14px;
  line-height: 162.88%;
  color: #E2B8AF;
}

.footer_site {
  margin-top: 7px;
  display: flex;
  align-items: center;
}

.footer_site:hover .footer_site--img {
  opacity: 1;
}

.footer_site:hover .footer_site--p {
  color: #000;
}

.footer_site:hover {
  text-decoration: none;
}

.footer_site--img {
  width: 17px;
  height: 17px;
  opacity: .2;
  transition: .4s all;
}

.footer_site--p {
  color: #374A48;
  font-size: 12px;
  margin-left: 7px;
  transition: .4s all;
}

.footer_ul {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer_li {
  margin: 0 7.5px;
}

.footer_li a {
  font-size: 11px;
  line-height: 15px;
  color: #E2B8AF;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'OpenSansBold';
}

.footer_li a:hover {
  text-decoration: none;
}

.footer_li a::after {
  display: block;
  content: '';
  border-bottom: 1px solid #E2B8AF;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform 450ms ease-in-out;
}

.footer_li a:hover::after {
  transform: scaleX(1);
  transform-origin: 0% 50%;
}

.footer_geo {
  margin-top: 7px;
  display: flex;
  align-items: center;
}

.footer_geo p {
  margin-left: 5px;
  margin-bottom: 0;
  font-size: 12px;
  line-height: 14px;
  color: #B6A39B;
}

.footer_mail {
  margin-top: 7px;
  display: flex;
  align-items: center;
}

.footer_mail a {
  text-decoration: underline;
  font-size: 12px;
  line-height: 14px;
  color: #B6A39B;
  margin-left: 5px;
  margin-bottom: 0;
}

.footer_mail a:hover {
  text-decoration: none;
}

.footer_tel {
  display: flex;
  align-items: center;
}

.footer_tel .tel {
  font-size: 24px;
  line-height: 28px;
  letter-spacing: 0.065em;
  color: #FFFFFF;
  margin-left: 5px;
  margin-bottom: 0;
  font-family: 'RobotoMedium';
}

.footer_soc {
  margin-top: 20px;
  display: flex;
  align-items: center;
}

.footer_soc--item {
  margin-right: 10px;
}

.intro {
  min-height: 522px;
  background: url(../img/intro.jpg) center / cover no-repeat;
}

.intro_block {
  max-width: 800px;
  padding-top: 100px;
  margin: auto;
}

.intro_h1 {
  font-size: 24px;
  line-height: 143.88%;
  text-align: center;
  color: #000000;
  font-family: 'Roboto Mono';
  font-weight: bold;
  max-width: 480px;
  margin: auto;
}

.intro_p {
  text-align: right;
  font-size: 24px;
  color: #000000;
  font-family: 'Roboto Mono';
  font-weight: bold;
}

.hello {
  padding: 120px 0 150px;
}

.hello_row {
  display: flex;
  align-items: flex-start;
  position: relative;
}

.hello_img {
  margin-left: 70px;
}

.hello_text {
  margin: 40px 0 0 100px;
  font-family: 'Roboto Mono';
  font-weight: normal;
  color: #000;
  font-size: 16px;
  line-height: 162.88%;
  max-width: 725px;
}

.hello_text--title {
  margin-bottom: 50px;
  display: block;
}

.hello_text--row {
  margin-top: 60px;
  display: flex;
  align-items: center;
}

