@media screen and (max-width: 1300px) {
  .about_block {
    width: 100%;
    padding: 60px 40px;
    min-height: inherit;
  }

  .about_text {
    padding-right: 20px;
  }

  .bottom_block {
    width: 100%;
  }
}

@media screen and (max-width: 1075px) {
  .footer_nav {
    display: none;
  }
}

