@media screen and (max-width: 991px) {
  .btn_mnu {
    width: 50px;
    height: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
  }

  .btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #557571;
    position: relative;
    transition: all .5s;
  }

  .btn_mnu.active .btn_row:first-child {
    transform: rotate(-135deg);
    top: 6px;
  }

  .btn_mnu.active .btn_row:last-child {
    transform: rotate(-225deg);
    top: -6px;
  }

  .btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
  }

  .btn_mnu.active .btn_row {
    background-color: #fff;
  }

  .btn_mnu.active {
    z-index: 101;
  }

  .header_nav {
    z-index: -10;
    opacity: 0;
    transition: .5s all;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header_nav.active {
    z-index: 100;
    opacity: 1;
  }

  .header_geo,
  .header_mail {
    display: none;
  }

  .header_ul {
    flex-direction: column;
  }

  .header_li {
    margin: 7px 0;
  }

  .header_tel {
    border-left: inherit;
    padding-left: 0px;
    margin-right: 15px;
  }

  .about {
    padding: 80px 0;
  }

  .about_block {
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .about_text h3 {
    position: absolute;
    top: 40px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    width: 100%;
    padding: 0 10px;
  }

  .about_img {
    order: -1;
    margin: 50px auto 30px;
  }

  .hello_img {
    margin-left: 20px;
